import React, { useState } from 'react'

import Home from '../Home'
import Page from '../Page'
import Feature from '../Feature'
import Footer from '../Footer/Footer'
import Join from '../Join/Join'
import GetWallet from '../GetWallet'
import Demo from '../Demo'

import './App.scss'

function App() {
  const [feature, setFeature] = useState({
    hand: '',
    face: '',
    independence: ''
  })

  const onMouseEnter = (key, value) => {
    if (feature[key] !== value) {
      setFeature({ [key]: value })
    }
  }

  const onMouseLeave = (key) =>
    setFeature({ [key]: null })

  return (
    <div className="App">
      <div id="page-1" className="Page-black">
        <Home />
      </div>

      <div id="page-3" className="Page-dark">
        <Page img="bc-musta"
              onMouseLeave={() => onMouseLeave('hand')}
              featureImg={feature.hand}
              text="For Members"
              info="Members are able to access a variety of private gyms and trainers"
              rtl dark>

          <Feature
            icon="non-custodial"
            onMouseEnter={() => onMouseEnter('hand', 'non-cost')}
            isActive={feature.hand === 'non-cost'}
            title="Workout privately"
            info="Members are able to enjoy a private workout experience" />

          <Feature
            icon="wallet"
            onMouseEnter={() => onMouseEnter('hand', 'wallet')}
            isActive={feature.hand === 'wallet'}
            title="Access a variety"
            info="Members are able to book at any gym on the platform" />

          <Feature
            icon="blocks"
            onMouseEnter={() => onMouseEnter('hand', 'portfolio')}
            isActive={feature.hand === 'portfolio'}
            title="Hop around"
            info="Members are able to workout at multiple gyms" />
        </Page>
      </div>
      <div id="page-4" className="Page-black">
        <Page img="bc-yak"
              onMouseLeave={() => onMouseLeave('independence')}
              featureImg={feature.independence}
              text="For Owners"
              info="Owners have the option to rent out their private gym spaces and earn moneys">

          <Feature
            icon="swap"
            onMouseEnter={() => onMouseEnter('independence', 'swap')}
            isActive={feature.independence === 'swap'}
            title="Re-occuring"
            info="Owners can earn re-occuring revenues by renting out their gyms" />

          <Feature
            icon="chart"

            onMouseEnter={() => onMouseEnter('independence', 'market')}
            isActive={feature.independence === 'market'}
            title="Earn Extra"
            info="Members earn passive income for renting our their gyms." />

          <Feature
            icon="ring"
            onMouseEnter={() => onMouseEnter('independence', 'search')}
            isActive={feature.independence === 'search'}
            title="Special Deals"
            info={
              <>
                Your members receive the latest deals from your gym.
              </>
            } />

        </Page>
      </div>
      <div id="page-5" className="Page-dark">
        <Page img="bc-leo"
              onMouseLeave={() => onMouseLeave('face')}
              featureImg={feature.face}
              text="For Trainers"
              info="Trainers have the option to select which gyms they want to work at"
              rtl>

          <Feature
            icon="user"
            onMouseEnter={() => onMouseEnter('face', 'no-data')}
            isActive={feature.face === 'no-data'}
            title="Profile"
            info="Create a trainer profile allowing you to train and workout at a gym" />

          <Feature
            icon="stop"
            onMouseEnter={() => onMouseEnter('face', 'no-kyc')}
            isActive={feature.face === 'no-kyc'}
            title="No confusion"
            info="Easily manage your trainers schedule through the plaform" />

          <Feature
            icon="incognito"
            onMouseEnter={() => onMouseEnter('face', 'privacy')}
            isActive={feature.face === 'privacy'}
            title="Virtual"
            info="Host virtual trainign experiences from the comfort of your home or at the gym" />

        </Page>
      </div>

      <div className="Page-black">
        <Demo />
      </div>

      <div className="Page-black">
        <div className="Wallet-download">
          <GetWallet />
        </div>
      </div>

      <div id="page-8" className="Page-dark">
        <Join />
      </div>

      <div id="page-9" className="Page-dark">
        <Footer />
      </div>
    </div>
  )
}

export default App
