import React, { useState } from 'react'
import cn from 'classnames'
import Container from '../Container'
import YoutubeEmbed from '../Video/YoutubeEmbed'
import './Demo.scss'

import { ReactComponent as Members } from './members.svg'
import { ReactComponent as Owners } from './owners.svg'
import { ReactComponent as Trainers } from './trainers.svg'
import { ReactComponent as LongDemo } from './ldemo.svg'

function Demo() {
  const [active, setActive] = useState('bitcoinWallet')
  const isActive = name => active === name ? 'active' : null
  const setState = val => () => setActive(val)

  const map = {
    bitcoinWallet: '0QAhxM4GUgQ',
    multiWallet: '',
    nftWallet: '',
    watchAddress: '',
  }

  return (
    <Container>
      <div className="Demo Section-space-top Section-space-bottom">
        <div className="Page-header-text text-center pb-5 text-gradient">
          Demo Videos
        </div>

        <div className="Demo-container mt-5 pt-5">
          <div className="Demo-content">
            <TabContent key={active} isActive={isActive}>
              <YoutubeEmbed embedId={map[active]} />
            </TabContent>
          </div>
          <div className="Demo-tabs">
            <div className="tablink-header">Demo Playlist</div>
            <Tab item="bitcoinWallet" Icon={Members} name="Members" isActive={isActive} setActive={setState} />
            <Tab item="multiWallet" Icon={Owners} name="Owners" isActive={isActive} setActive={setState} />
            <Tab item="nftWallet" Icon={Trainers} name="Trainers" isActive={isActive} setActive={setState} />
            <Tab item="watchAddress" Icon={LongDemo} name="Long Demo" isActive={isActive} setActive={setState} />
          </div>
        </div>
      </div>
    </Container>
  )
}


const Tab = ({ name, item, Icon, isDisabled, isActive = () => false, setActive = () => false }) => (
  <div className={cn('tablink', isActive(item), { disabled: isDisabled })} onClick={setActive(item)}>
    <Icon />
    <span className="ms-3">{name}</span>
  </div>
)

const TabContent = ({ children }) => (
  <div className="tabcontent">
    {children}
  </div>
)

export default Demo
